.auth{min-width:300px;max-width:400px;padding-top:58px;margin:0 auto}.auth__title{margin:6px 0 24px;font-size:22px;font-weight:700;line-height:28px;text-align:center;color:#152242}.auth__widget{padding:0 30px;margin:auto;width:320px}.auth__content{margin-bottom:16px}.auth__input{padding:7px 12px 9px;width:100%;height:40px;font-size:16px;color:#152242;background-color:#fff;border:1px solid #d0d3d9;border-radius:4px;outline:none;transition:border .15s,color .15s}.auth__button{color:#fff;background-color:#0468ff;width:100%;position:relative;margin-bottom:12px;display:inline-flex;height:40px;font-size:16px;font-weight:700;text-align:center;cursor:pointer;user-select:none;border:none;border-radius:4px;outline:none;backface-visibility:hidden;justify-content:center;align-items:center}.auth__authentication{flex-grow:1;max-width:100%}.auth__container{position:relative;padding-top:11px;margin:31px 0 24px;text-align:center}.auth__container:after{position:absolute;top:-10px;left:50%;padding:0 16px;height:20px;font-size:14px;line-height:1;content:"или";color:#7a7a7a;background:#fff;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.auth__link{color:#2b87db;text-decoration:none;cursor:pointer;font-size:14px;line-height:20px}